import { h, Component } from 'preact';
import { observer } from 'mobx-preact';

import LineGraph from '../../components/lineGraph';
import Map from '../../components/map';
import wutil from '../../lib/wifet-util';

const windData = wutil.makeRandomData({ cnt: 500, min: 0, max: 30, onlyUp: false, diff: 1, maxSkew: 10 });

const productionData = wutil.calcData(windData, wutil.calcProductionData);
const productionBoostData = wutil.addPercent(productionData, 10, 100);
const productionLongLifeData = wutil.addPercent(productionData, -5, 100);

const initialState = {
	productionData,
	productionBoostData,
	productionLongLifeData,
};

@observer
class Production extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	render() {
		const { productionData, productionBoostData, productionLongLifeData } = this.state;
		const { appState, windmillStore } = this.props.stores;
        const { darkmode } = appState;
		const { windmills } = windmillStore;

		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1><i class='fas fa-chart-bar' /> Production</h1>
						<LineGraph
							stores={this.props.stores}
							data={productionData}
							dataB={productionBoostData}
							dataC={productionLongLifeData}
							legend='Normal production'
							legendB='Boost 10%'
							legendC='Long life -5%'
							width={1200}
							height={400}
							yMax={500}
							yMin={0}
							showYTicks={1}
							yTicks='["500Mw", "400Mw", "300Mw", "200Mw", "100Mw", "0Mw"]'
							unit='Mw'
							showXTicks={1}
							marker={400}
						/>
					</div>
				</div>
				<div class='row'>
					<div class='col-12' style='height: 50vh;'>
						<Map stores={this.props.stores}
							darkmode={darkmode}
							center={[67.1253653, 12.422578125]}
							zoom={13}
							waypoints={windmills}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Production;
